<template>
  <div id="app">
    <div class="course-main">
      <div class="course-filter-wrapper">
        <div class="course-filter-header clearfix">
          <div v-for="filter_type in filter_types" class="course-filter-item">
            <a href="#" v-bind:class="{'active': filter_type.is_active}" @click="showDetail($index, $event)"><span class="f-text">{{filter_type.text}}</span><span class="course-caret"></span></a>
          </div>
        </div>
        <div class="course-filter-body clearfix" v-show="show_details">
          <div class="course-filter-inner course-filter-inner-left">
            <div v-for="detail_item in current_filter_details" class="course-filter-body-item">
              <a v-bind:class="{'active': detail_item.is_active}" href="#" @click="showSubDetail($index)">{{detail_item.text}}</a>
            </div>
          </div>
          <div class="course-filter-inner course-filter-inner-right">
            <div v-for="sub_detail_item in current_sub_filter_details" class="course-filter-body-item">
              <a v-bind:class="{'active': sub_detail_item.is_active}" @click="clickSubDetail($index)" href="#">{{sub_detail_item.text}}</a>
            </div>
          </div>
        </div>
      </div>
      <div class="p-course-filter-wrapper">
        <div class="p-course-inner-filter">
          <div v-for="filter_type in filter_types" class="p-single-filter clearfix">
            <div class="p-single-filter-title">{{filter_type.text}}</div>
            <div class="p-single-filter-body clearfix">
              <div v-for="filter_detail in filter_details[filter_type.f_index]" class="p-single-filter-item" v-bind:class="{'big-margin': current_sub_filter_details.length!=0 && filter_detail.children.length!=0 && filter_detail.is_active}">
                <a v-bind:class="{'active': filter_detail.is_active}" @click="showSubDetailP(filter_type.f_index, $index)" href="#">{{filter_detail.text}}</a>
                <span v-if="filter_detail.children.length!=0" v-show="filter_detail.is_active && current_sub_filter_details.length!=0" class="icon"></span>
                <div v-if="filter_detail.children.length!=0" v-show="filter_detail.is_active && current_sub_filter_details.length!=0" class="p-single-filter-sub-items clearfix">
                  <a v-for="filter_sub_detail in filter_detail.children" href="#" v-bind:class="{'active': filter_sub_detail.is_active}" @click="clickSubDetailP($index)">{{filter_sub_detail.text}}</a>
                </div>
              </div>
           <!--    <div class="p-single-filter-item">
                <a href="#">进行中{{$parent.$index}}</a>
                <span class="icon"></span>
                <div class="p-single-filter-sub-items clearfix">
                  <a href="#">一周内</a>
                  <a href="#">一个月内</a>
                  <a href="#">半年内</a>
                </div>
              </div> -->
            </div>
          </div>
        </div>
      </div>
      <div class="course-items-wrapper clearfix">
        <div v-for="item in course_items" class="course-item">
          <div class="course-item-wrapper">
            <a class="course-item-inner" v-bind:href="item.location_url">
              <img v-bind:src="item.ad_photo | imageprefix" alt="">
              <span class="course-item-title">{{item.ad_title}}</span>
            </a>
            <div class="course-item-text">{{item.ad_say}}</div>
            <div class="course-item-icons clearfix">
              <div class="course-item-sub-icons1 clearfix">
                <div class="course-icon course-loca-icon"><img src="./assets/icon5.png" alt="">&nbsp;<span>{{item.ad_adress | truncateAddress}}</span></div>
                <div class="course-icon course-time-icon"><img src="./assets/icon4.png" alt="">&nbsp;<span>{{item.ad_starttime}}~{{item.ad_stoptime}}</span></div>
              </div>
              <div class="course-item-sub-icons2 clearfix">
                <div class="course-icon course-like-icon"><img src="./assets/icon3.png" alt="">&nbsp;<span>{{item.ad_readnum}}</span></div>
                <div class="course-icon course-praise-icon"><img src="./assets/icon2.png" alt="">&nbsp;<span>{{item.ad_agreenum}}</span></div>
                <div class="course-icon course-view-icon"><img src="./assets/icon1.png" alt="">&nbsp;<span>{{item.ad_savenum}}</span></div>
              </div>
            </div>
          </div>
        </div>
        <div class="get-more-item" v-show="!is_getting_list&&have_more">
          <span class="white-line"></span>
          <a @click.prevent="getMore" href="#"><span>more</span><img src="./assets/down.png" alt=""></a>
        </div>
        <div class="loading-gif" v-show="is_getting_list">
          <img src="./assets/loading.gif" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var static_prefix;
if (window.__public__) {
  static_prefix = window.__public__;
}
else {
  static_prefix = '/';
}
var url;
if (window.__url__) {
  url = window.__url__;
}
else {
  url = 'http://121.42.207.83/yoga/index.php/Home/Active/active_list';
}



export default {
  ready () {
    this.is_getting_list = true
    this.$http.get(url, {params: {page_num: 1, time: this.params[0], place: this.params[1]}}).then((response) => {
      console.log(response)
      this.is_getting_list = false
      var responseData = response.data
      if (typeof responseData === 'string') {
        responseData = JSON.parse(responseData)
      }
      if (responseData.data) {
        var items = Object.values(responseData.data)
        this.course_items = this.course_items.concat(items)
        console.log(this.course_items)
      }
      
      this.have_more = response.data.have_more
    }, 
    (response) => {
      console.log('error')
      this.is_getting_list = false
    })
    // console.log(window.x)
  },
  data () {
    return {
      filter_types: [
        {text: '时间', is_active: false, f_index: 0},
        {text: '地点', is_active: false, f_index: 1}
      ],
      filter_details: [
        [
          {text: '全部', param: 0, is_active: true, children: []},
          {text: '进行中', param: '2', is_active: false, children: [{text: '一周内', param: 'week', is_active: false}, {text: '一个月内', param: 'month', is_active: false}, {text: '半年内', param: 'year', is_active: false}]},
          {text: '往期', param: 'else', is_active: false, children: []}
        ],
        [
          {text: '全部', param: 0, is_active: true, children: []},
          {text: '北京', param: '北京', is_active: false, children: []},
          {text: '上海', param: '上海', is_active: false, children: []},
          {text: '广州', param: '广州', is_active: false, children: []},
          {text: '成都', param: '成都', is_active: false, children: []},
          {text: '国内其他', param: '国内其他', is_active: false, children: []},
          {text: '境外', param: '境外', is_active: false, children: []}
        ]
      ],
      filter_type_details: {},
      current_filter_details: [],
      current_sub_filter_details: [],
      page_num: 1,
      course_items: [],
      show_details: false,
      params: [0, 0],
      current_filter_index: 0,
      is_getting_list: false,
      have_more: 1
    }
  },
  methods: {
    showDetail (index, event) {
      /* 筛选栏状态切换 */
      var len = this.filter_types.length
      for (var i = 0; i < len; i++) {
        this.filter_types[i].is_active = false
      }
      this.filter_types[index].is_active = true
      this.show_details = true
      this.current_filter_index = index

      /* 显示第一级筛选细节 */
      this.current_filter_details = this.filter_details[index]
      /* 显示第二级筛选细节 */
      var which_detail;
      var len2 = this.current_filter_details.length
      for (var i = 0; i < len2; i++) {
        if (this.current_filter_details[i].is_active == true) {
          this.current_sub_filter_details = this.current_filter_details[i].children
        }
      }
    },
    startAjax () {
      this.course_items = []
      console.log(this.page_num)
      console.log(this.params[0])
      console.log(this.params[1])
      console.log(url)
      this.is_getting_list = true
      this.$http.get(url, {params: {page_num: 1, time: this.params[0], place: this.params[1]}}).then((response) => {
        console.log(response)
        var responseData = response.data
        if (typeof responseData === 'string') {
          responseData = JSON.parse(responseData)
        }
        if (responseData.data) {
          var items = Object.values(responseData.data)
          this.course_items = items
          console.log(this.course_items)
        }
        this.have_more = responseData.have_more
        this.is_getting_list = false
      }, 
      (response) => {
        this.is_getting_list = false
        console.log('error')
      })
    },
    getMore () {
      this.page_num += 1
      this.startAjaxMore()
    },
    startAjaxMore () {
      this.is_getting_list = true
      this.$http.get(url, {params: {page_num: this.page_num, time: this.params[0], place: this.params[1]}}).then((response) => {
        var responseData = response.data
        if (typeof responseData === 'string') {
          responseData = JSON.parse(responseData)
        }
        if (responseData.data) {
          var items = Object.values(responseData.data)
          this.course_items = this.course_items.concat(items)
          console.log(this.course_items)
        }
        this.have_more = responseData.have_more
        console.log(this.have_more)
        this.is_getting_list = false
      }, 
      (response) => {
        this.is_getting_list = false
        console.log('error')
      })
    },
    showSubDetail (index) {
      this.current_sub_filter_details = []
      this.params[this.current_filter_index] = this.current_filter_details[index].param

      var len = this.current_filter_details.length
      for (var i=0; i<len; i++) {
        this.current_filter_details[i].is_active = false
      }
      this.current_filter_details[index].is_active = (this.params[this.current_filter_index]==this.current_filter_details[index].param)


      if (this.current_filter_details[index].children.length != 0) {
        this.current_sub_filter_details = this.current_filter_details[index].children;
        return;
      } else {
        this.show_details = false
        this.filter_types[this.current_filter_index].is_active = false

        if (this.current_filter_index == 3) {
          /* 修改第四级筛选列表 */
        }

        this.page_num = 1
        /* start ajax */
        this.startAjax()
      }
    },
    clickSubDetail (index) {
      /* 显示样式 */
      var len = this.current_sub_filter_details.length
      for (var i=0; i<len; i++) {
        this.current_sub_filter_details[i].is_active = false
      }
      this.current_sub_filter_details[index].is_active = true

      /* 修改ajax参数 */
      this.params[this.current_filter_index] = this.current_sub_filter_details[index].param

      this.show_details = false
      this.filter_types[this.current_filter_index].is_active = false

      this.current_sub_filter_details = []
      /* start ajax */
      this.page_num = 1
      this.startAjax()
    },
    showSubDetailP (f_index, index) {
      /* 修改ajax参数 */
      this.params[f_index] = this.filter_details[f_index][index].param
      var len = this.filter_details[f_index].length
      for (var i=0; i<len; i++) {
        this.filter_details[f_index][i].is_active = false
      }
      this.filter_details[f_index][index].is_active = true

      if (this.filter_details[f_index][index].children.length == 0) {
        this.page_num = 1
        this.startAjax()
      } else {
        this.current_sub_filter_details = this.filter_details[f_index][index].children
      }
      return;
    },
    clickSubDetailP (index) {
      var filter_sub_details = this.filter_details[0][1].children
      this.params[0] = filter_sub_details[index].param
      var len = filter_sub_details.length
      console.log(filter_sub_details)
      for (var i=0; i<len; i++) {
        filter_sub_details[i].is_active = false
      }
      filter_sub_details[index].is_active = true
      this.current_sub_filter_details = []
      this.page_num = 1
      this.startAjax()
    }
  }
}
</script>

