import Vue from 'vue'
import App from './App'
import {truncateAddress} from './filters'
import VueResource from 'vue-resource'

Vue.use(VueResource)

Vue.filter('truncateAddress', truncateAddress)

var static_prefix;
if (window.__public__) {
  static_prefix = window.__public__;
}
else {
  static_prefix = '/';
}

Vue.filter('imageprefix', function(value) {
  return static_prefix + value
})

/* eslint-disable no-new */
new Vue({
  el: 'body',
  components: { App }
})



